var THREE = require('three');
var canvg = require('./canvg.js');
window.canvg = canvg;
window.THREE = THREE; // expose the THREE variable to the debug console


var scene = new THREE.Scene();
var camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 1, 10000);
var renderer = new THREE.WebGLRenderer();

/** begin wheel defs */
var wheelSVG = require('file!./wheel2.svg');
var wheelCanvas = document.createElement("canvas");
wheelCanvas.height = wheelCanvas.width = 2048;
canvg(wheelCanvas, wheelSVG);
var wheelTexture = new THREE.Texture(wheelCanvas);
wheelTexture.needsUpdate = true;
var wheelGeometry = new THREE.PlaneGeometry(1024, 1024);
var wheelMaterial = new THREE.MeshBasicMaterial({
  map: wheelTexture
});
var wheelMesh = new THREE.Mesh(wheelGeometry, wheelMaterial);
/** end wheel defs */

function init() {
  scene = new THREE.Scene();

  camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 1, 10000);
  camera.position.z = 250;

  wheelMesh.position.y = -100;
  scene.add(wheelMesh);

  renderer = new THREE.WebGLRenderer();
  renderer.setSize(window.innerWidth, window.innerHeight);

  document.body.appendChild(renderer.domElement);
}


function animate() {
  requestAnimationFrame(animate);
  wheelMesh.rotation.z += 0.01;
  renderer.render(scene, camera);
}

import {getEvents} from './labels2events.js';
import {startScheduler} from './scheduler.js';

/** @type {Object[]} */
var eventList = getEvents();
var eventStream = document.createElement(null);


eventStream.addEventListener('B', event => {
  kick();
  console.log("Kick " + event.detail.time.toString() + " " + audio.currentTime.toString());
});

/** */
function letsJam() {
  /** start the renderer */
  init();
  animate();

  /** start the music and the scheduler */
  audio.play();
  startScheduler(audio, eventList, eventStream);

  /** remove the curtain */
  TweenMax.to("#curtain", 3, {
    opacity: 0,
    ease: Sine.easeIn,
    onComplete: () => {
      document.body.removeChild(curtain);
    }
  });

  /** for debugging */
  window.audio = audio;
  console.log(audio.currentTime);
}

var url = require('file!./zettai_sample.ogg');

document.body.onclick = function(e) {
  document.body.onclick = null;
  loadSong(url);
};



