/* eslint-env browser */

/** @type {Object[]} */
var labels = require('json!./test labels.json').labels;
/** @type {Object[]} */
var eventList = [];

/** @return {Object[]} An array of events, sorted in ascending order by time.*/
export function getEvents() {
  // for each label, convert it to be used as an event
  labels.forEach((label, i, array) => {
    if (label.end) {
      // duration label, split into _open and _close events
      eventList.push({
        name: label.name + "_open",
        time: label.start
      });
      eventList.push({
        name: label.name + "_close",
        time: label.end
      });
    } else {
      // point label, single event
      eventList.push({
        name: label.name,
        time: label.start
      });
    }
  });

  // sort the events in ascending order by time
  eventList.sort((a, b) => {
    return a.time - b.time;
  });
  return eventList;
}

module.exports = {
  getEvents: getEvents
};



