/* eslint-env browser */


var audioEventStream = document.createElement(null);

/** @type {AudioContext} */
var ctx;
/** @type {Object[]} */
var events;
/** @type {Number} duration to look ahead in seconds*/
var lookAheadDuration = 0.30;
var safety = 0.15;

var index = 0;

/** */
function lookAhead() {
  var currentTime = ctx.currentTime;
  var latest = currentTime + lookAheadDuration;
  // if the next event is in the period we're looking at
  if (index < events.length && events[index].time <= latest) {
    while (events[index].time <= latest) {
      setTimeout(
        sendEvent,
        (events[index].time - currentTime - safety) * 1000,
        events[index]
      );
      index++;
    }
  }
  setTimeout(lookAhead, 10);
}

/** */
function sendEvent(event) {
  // fire event with its timecode
  audioEventStream.dispatchEvent(
    new CustomEvent(event.name, {
      detail: {
        time: event.time
      }
    })
  );
}

/** Start the event scheduler.
 *  @param {AudioContext} context the AudioContext to synchronize events with
 *  @param {Object[]} eventList the list of events to schedule
 *  @param {EventTarget} eventTarget the EventTarget to dispatch events to
*/
export function startScheduler(context, eventList, eventTarget) {
  audioEventStream = eventTarget;
  events = eventList;
  ctx = context;

  lookAhead();
}

module.exports = {
  startScheduler: startScheduler
};



